@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-primary: 'Poppins', sans-serif;

  --colorT-lilac: rgba(176,172,251, 1);
  --colorT-lila: rgba(65,57,255, 1);
  --colorT-gris: rgba(135, 135, 148, .3);
  --colorT-rojo: rgba(245, 5, 16, 1);
  --colorT-bordo: rgba(115, 6, 11, 1);
  --colorT-celeste: rgba(52,  150,  175, 1);

  --color-blanco: #ffffff;
  --color-negro: #000000;
  --color-celeste:#ddecff;
  --color-azul: #0A4580;
  --color-gris: #6c757d;

  

}

*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--font-primary);
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}


body{
  
}

header{
    width: 100%;
    background: var(--colorT-celeste);
    position:fixed;
    z-index: 1000;
    
}
.nav{
    width: 100%;
    min-height: 100vh;
    background: var(--color-celeste);
    opacity: 0;
    visibility: hidden;
    transition: ease 1s;
    position: absolute;
    right: -100%;
    z-index: -1000;
}

.nav-options{
    padding: 2vh 0 2vh 0;
    display: flex;
    align-items: center;
}
.nav-options img{
    padding: 0px 25px 0px 35px;
    
}
.span{
    
    font-size: 1.5em;
    font-weight: 600;
    /*line-height: normal;*/
    color: var(--color-gris);
}
.span:hover{
    transition: 0.2s ease;
    cursor: pointer;
    color: #fff;
    text-shadow:     
    0 0 5px rgba(0,178,255,1),
    0 0 10px rgba(0,178,255,1),
    0 0 20px rgba(0,178,255,1);
    
}
.toggle{
    cursor: pointer;
    width: 60px;
    height: 60px;
    position: relative;
    z-index: 1000;
}

/*--------------Creacion de la hamburguesa-----------------*/
.toggle span, .toggle span:before, .toggle span:after{
    display: block;
    width: 45px;
    height: 4.5px;
    background-color: var(--color-gris);
    position: absolute;
    color: #fff;
    transition: ease .5s;
}
.toggle span:before, .toggle span:after{
    content: '';
}

.toggle span{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.toggle span:after{
    bottom: 14px;
}
.toggle span:before{
    top: 14px;
}

/*-------------------------------fin hamburguesa------------*/

/*----------------javaScript--------------------*/
.toggle.close span{
    transform: rotate(45deg);
}
.toggle.close span:before{
    top: 0px;
    transform: rotate(90deg);
}
.toggle.close span:after{
    opacity: 0;
}

.show{
    opacity: 1;
    visibility: visible;
    right: 0;
}



.inicio{
    height:100vh;
    
}
.inicio-content{
    min-width: 360px;
    padding: 0 20px 0 20px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 210px;
    padding-top: 130px;
    
    justify-items: center;
    align-items: center;
}

.inicio-p{
    grid-column: 1/3;
    font-size: 1.4em;
    color: var(--color-gris)
    
}

.inicio-icons{
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-items: center;
    
}
.i-linkedin > img, .i-github > img{
    width:60px;
}
.i-linkedin{
    grid-column: 1/2;
    grid-row: 1/2;
}
.i-github{
    grid-column:2/3;
    grid-row: 2/3;
}

.inicio-img{
    border-radius: 50%;
    width: 210px;
    grid-column: 2/3;
    grid-row: 2/3;
}

.border-grid{
    border: 1px solid #000;
}
