.item-content{
    border: 1px solid #eee;
    box-shadow: 0 2px 2px #ccc;
    padding: 15px;
    display: flex;
    flex-wrap: wrap;
    transition: .5s ease;
    
}
.item-content:hover{
    box-shadow: 0 2px 2px var(--color-gris);
    cursor: pointer;
    
}
.item-content > img{
    padding: 0 25px 0 15px;
    margin-right: 30px;
    border-right: 1px solid rgba(187, 184, 184, .4);

}

.item-content > span {
    color: var(--color-gris);
    align-self: center;
    font-size: 1.3em;
    
}





